"use client";

import { useState } from "react";
import { useUser } from "@/contexts/UserContext";
import { JobService } from "@/services/jobService";
import {
  Bookmark,
  BookmarkCheck,
  Briefcase,
  MapPin,
  Calendar,
  DollarSign,
} from "lucide-react";

interface JobListingCardProps {
  id: string;
  companyName: string;
  companyLogo: string;
  jobTitle: string;
  jobType: "full-time" | "part-time" | "contract";
  remoteLevel: "remote" | "hybrid" | "onsite";
  location: string;
  salaryMin: number;
  salaryMax: number;
  salaryPeriod: "year" | "month" | "hour";
  techStack: string[];
  postedDate: Date;
  source: string;
  sourceUrl: string;
  isSaved?: boolean;
  onSave?: (id: string, saved: boolean) => void;
  onApply?: (id: string) => void;
}

export function JobListingCard({
  id,
  companyName,
  companyLogo,
  jobTitle,
  jobType,
  remoteLevel,
  location,
  salaryMin,
  salaryMax,
  salaryPeriod,
  techStack,
  postedDate,
  source,
  sourceUrl,
  isSaved = false,
  onSave,
  onApply,
}: JobListingCardProps) {
  const { user } = useUser();
  const [saved, setSaved] = useState(isSaved);
  const [applying, setApplying] = useState(false);
  const [saving, setSaving] = useState(false);

  const formatSalary = () => {
    const format = (amount: number) => {
      if (salaryPeriod === "year") return `$${(amount / 1000).toFixed(0)}k`;
      if (salaryPeriod === "month") return `$${amount}/mo`;
      return `$${amount}/hr`;
    };

    if (salaryMin && salaryMax) {
      return `${format(salaryMin)} - ${format(salaryMax)}`;
    } else if (salaryMin) {
      return `From ${format(salaryMin)}`;
    } else if (salaryMax) {
      return `Up to ${format(salaryMax)}`;
    }
    return "Salary not specified";
  };

  const handleSave = async () => {
    if (!user) {
      alert("Please sign in to save jobs");
      return;
    }

    setSaving(true);
    try {
      if (saved) {
        await JobService.unsaveJob(user.id, id);
        setSaved(false);
        onSave?.(id, false);
      } else {
        await JobService.saveJob(user.id, id);
        setSaved(true);
        onSave?.(id, true);
      }
    } catch (error) {
      console.error("Error saving job:", error);
    } finally {
      setSaving(false);
    }
  };

  const handleApply = async () => {
    if (!user) {
      alert("Please sign in to apply for jobs");
      return;
    }

    setApplying(true);
    try {
      await JobService.applyToJob(user.id, id);

      // Open the actual job application page
      if (sourceUrl && sourceUrl !== "#") {
        window.open(sourceUrl, "_blank");
      }

      onApply?.(id);
      alert("Application recorded! Good luck! ðŸš€");
    } catch (error) {
      console.error("Error applying to job:", error);
    } finally {
      setApplying(false);
    }
  };

  const getRemoteBadgeColor = (level: string) => {
    switch (level) {
      case "remote":
        return "bg-green-100 text-green-800";
      case "hybrid":
        return "bg-blue-100 text-blue-800";
      case "onsite":
        return "bg-gray-100 text-gray-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  const getJobTypeColor = (type: string) => {
    switch (type) {
      case "full-time":
        return "bg-purple-100 text-purple-800";
      case "part-time":
        return "bg-orange-100 text-orange-800";
      case "contract":
        return "bg-red-100 text-red-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  return (
    <div className="bg-white rounded-lg border border-gray-200 p-6 hover:shadow-md transition-shadow">
      <div className="flex justify-between items-start mb-4">
        <div className="flex items-center space-x-4">
          <div className="w-12 h-12 bg-gray-100 rounded-lg flex items-center justify-center">
            {companyLogo ? (
              <img
                src={companyLogo}
                alt={companyName}
                className="w-10 h-10 object-contain rounded"
              />
            ) : (
              <div className="text-lg font-bold text-gray-400">
                {companyName.charAt(0).toUpperCase()}
              </div>
            )}
          </div>
          <div>
            <h3 className="font-semibold text-lg text-gray-900">{jobTitle}</h3>
            <p className="text-gray-600">{companyName}</p>
          </div>
        </div>

        <button
          onClick={handleSave}
          disabled={saving}
          className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
          title={saved ? "Remove from saved" : "Save job"}
        >
          {saved ? (
            <BookmarkCheck className="w-5 h-5 text-blue-600" />
          ) : (
            <Bookmark className="w-5 h-5 text-gray-400 hover:text-blue-600" />
          )}
        </button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
        <div className="space-y-2">
          <div className="flex items-center space-x-2 text-sm text-gray-600">
            <MapPin className="w-4 h-4" />
            <span>{location}</span>
          </div>
          <div className="flex items-center space-x-2 text-sm text-gray-600">
            <Briefcase className="w-4 h-4" />
            <span>{jobType}</span>
          </div>
          <div className="flex items-center space-x-2 text-sm text-gray-600">
            <DollarSign className="w-4 h-4" />
            <span>{formatSalary()}</span>
          </div>
        </div>

        <div className="space-y-2">
          <div className="flex flex-wrap gap-2">
            <span
              className={`px-2 py-1 rounded-full text-xs font-medium ${getRemoteBadgeColor(
                remoteLevel
              )}`}
            >
              {remoteLevel}
            </span>
            <span
              className={`px-2 py-1 rounded-full text-xs font-medium ${getJobTypeColor(
                jobType
              )}`}
            >
              {jobType}
            </span>
          </div>
          <div className="flex items-center space-x-2 text-sm text-gray-500">
            <Calendar className="w-4 h-4" />
            <span>Posted {postedDate.toLocaleDateString()}</span>
          </div>
        </div>
      </div>

      {techStack.length > 0 && (
        <div className="mb-4">
          <div className="flex flex-wrap gap-2">
            {techStack.slice(0, 4).map((tech, index) => (
              <span
                key={index}
                className="px-2 py-1 bg-gray-100 text-gray-700 rounded-md text-xs"
              >
                {tech}
              </span>
            ))}
            {techStack.length > 4 && (
              <span className="px-2 py-1 bg-gray-100 text-gray-500 rounded-md text-xs">
                +{techStack.length - 4} more
              </span>
            )}
          </div>
        </div>
      )}

      <div className="flex justify-between items-center">
        <span className="text-sm text-gray-500">Via {source}</span>
        <button
          onClick={handleApply}
          disabled={applying}
          className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 disabled:opacity-50 flex items-center space-x-2"
        >
          <Briefcase className="w-4 h-4" />
          <span>{applying ? "Applying..." : "Apply Now"}</span>
        </button>
      </div>
    </div>
  );
}
