import { supabase } from '@/lib/supabase'

export interface Job {
  id: string
  title: string
  description: string
  company_id: string
  companies: {
    name: string
    logo_url: string
    headquarters_location: string
  }
  remote_level: 'remote' | 'hybrid' | 'onsite'
  job_type: 'full-time' | 'part-time' | 'contract'
  location: string
  salary_min: number
  salary_max: number
  salary_currency: string
  required_tech_stack: string[]
  experience_level: string
  posting_date: string
  created_at: string
  application_url?: string
}

export interface SavedJob {
  id: string
  job_id: string
  user_id: string
  saved_at: string
  notes: string
  priority: number
  jobs: Job
}

export interface JobApplication {
  id: string
  job_id: string
  user_id: string
  status: 'applied' | 'interviewing' | 'rejected' | 'offered' | 'accepted'
  applied_at: string
  cover_letter: string
  notes: string
  follow_up_date: string
  interview_date: string
  offer_details: any
  jobs: Job
}

export class JobService {
  static async getJobs(filters?: any): Promise<Job[]> {
    let query = supabase
      .from('jobs')
      .select(`
        *,
        companies (
          name,
          logo_url,
          headquarters_location
        )
      `)
      .eq('status', 'active')

    // Apply filters if provided
    if (filters?.searchQuery) {
      query = query.textSearch('title', filters.searchQuery)
    }

    if (filters?.remoteOptions?.length) {
      query = query.in('remote_level', filters.remoteOptions)
    }

    if (filters?.jobTypes?.length) {
      query = query.in('job_type', filters.jobTypes)
    }

    if (filters?.techStack?.length) {
      query = query.overlaps('required_tech_stack', filters.techStack)
    }

    // LOCATION FILTER
    if (filters?.locations?.length) {
      const locationConditions = filters.locations.map((location: any) => 
        `location.ilike.%${location}%,companies.headquarters_location.ilike.%${location}%`
      ).join(',')
      
      query = query.or(locationConditions)
    }

    const { data, error } = await query

    if (error) {
      console.error('Error fetching jobs:', error)
      return []
    }

    return data || []
  }

  // ADD THE MISSING SEARCHJOBS METHOD HERE:
  static async searchJobs(searchQuery: string): Promise<Job[]> {
    if (!searchQuery.trim()) {
      return this.getJobs()
    }

    const { data, error } = await supabase
      .from('jobs')
      .select(`
        *,
        companies (
          name,
          logo_url,
          headquarters_location
        )
      `)
      .eq('status', 'active')
      .or(`title.ilike.%${searchQuery}%,description.ilike.%${searchQuery}%,companies.name.ilike.%${searchQuery}%`)

    if (error) {
      console.error('Error searching jobs:', error)
      return []
    }

    return data || []
  }

  // SAVE JOB FUNCTIONALITY
  static async saveJob(userId: string, jobId: string, notes: string = '', priority: number = 1): Promise<boolean> {
    const { error } = await supabase
      .from('user_saved_jobs')
      .insert([
        {
          user_id: userId,
          job_id: jobId,
          notes,
          priority,
        },
      ])

    if (error) {
      console.error('Error saving job:', error)
      return false
    }
    return true
  }

  static async unsaveJob(userId: string, jobId: string): Promise<boolean> {
    const { error } = await supabase
      .from('user_saved_jobs')
      .delete()
      .eq('user_id', userId)
      .eq('job_id', jobId)

    if (error) {
      console.error('Error unsaving job:', error)
      return false
    }
    return true
  }

  static async getSavedJobs(userId: string): Promise<SavedJob[]> {
    const { data, error } = await supabase
      .from('user_saved_jobs')
      .select(`
        *,
        jobs (
          *,
          companies (
            name,
            logo_url,
            headquarters_location
          )
        )
      `)
      .eq('user_id', userId)
      .order('saved_at', { ascending: false })

    if (error) {
      console.error('Error fetching saved jobs:', error)
      return []
    }
    return data || []
  }

  // APPLICATION TRACKING
  static async applyToJob(
    userId: string, 
    jobId: string, 
    coverLetter: string = '', 
    notes: string = ''
  ): Promise<boolean> {
    const { error } = await supabase
      .from('user_applications')
      .insert([
        {
          user_id: userId,
          job_id: jobId,
          cover_letter: coverLetter,
          notes,
          status: 'applied',
        },
      ])

    if (error) {
      console.error('Error applying to job:', error)
      return false
    }
    return true
  }

  static async updateApplicationStatus(
    applicationId: string, 
    status: JobApplication['status'],
    notes?: string
  ): Promise<boolean> {
    const updates: any = { status }
    if (notes) updates.notes = notes

    const { error } = await supabase
      .from('user_applications')
      .update(updates)
      .eq('id', applicationId)

    if (error) {
      console.error('Error updating application:', error)
      return false
    }
    return true
  }

  static async getApplications(userId: string): Promise<JobApplication[]> {
    const { data, error } = await supabase
      .from('user_applications')
      .select(`
        *,
        jobs (
          *,
          companies (
            name,
            logo_url,
            headquarters_location
          )
        )
      `)
      .eq('user_id', userId)
      .order('applied_at', { ascending: false })

    if (error) {
      console.error('Error fetching applications:', error)
      return []
    }
    return data || []
  }

  static async getApplicationStats(userId: string) {
    const { data, error } = await supabase
      .from('user_applications')
      .select('status')
      .eq('user_id', userId)

    if (error) {
      console.error('Error fetching stats:', error)
      return null
    }

    const stats = {
      total: data.length,
      applied: data.filter(app => app.status === 'applied').length,
      interviewing: data.filter(app => app.status === 'interviewing').length,
      rejected: data.filter(app => app.status === 'rejected').length,
      offered: data.filter(app => app.status === 'offered').length,
      accepted: data.filter(app => app.status === 'accepted').length,
    }

    return stats
  }
}